import React, {useEffect, useState} from 'react';
import {
    Grid,
    Typography,
} from '@material-ui/core';
import Snackbar from '@mui/material/Snackbar';
import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import _ from 'lodash'
import ClearRoundedIcon from '@mui/icons-material/ClearRounded';
import CancelOutlinedIcon from '@mui/icons-material/CancelOutlined';

interface Props {
    children: JSX.Element | JSX.Element[];
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        main: {
            display: 'flex',
            width: '100%',
            backgroundColor:'#F9F9F9'
        },
        snackbar: {
            background: '#FFFFFF',
            boxShadow: '0px 4px 10px rgba(0, 0, 0, 0.1)',
            borderRadius: '5px',
            height: '62px',
            marginTop: '110px',
            padding:'20px 32px'
        },
    })
);



const SnackbarDev = (props: any): React.ReactElement<Props> =>
{
    
    const classes = useStyles()

    return(
        <Grid container direction='column' alignItems='center' className={classes.main}>
            <Snackbar anchorOrigin={{ vertical: 'top', horizontal: 'center' }} open={props.alertOpen} autoHideDuration={3000} onClose={() => props.setAlertOpen(false)}>
                <Grid container justifyContent='space-between' alignItems='center' className={classes.snackbar} >
                    <Grid item style={{marginRight:'10px'}}>
                        <Grid container>
                            <CancelOutlinedIcon style={{ color: '#FF4D4F', marginRight: '16px', width: '22px', height: '22px' }} />
                            <Typography>{props.wording}</Typography>
                        </Grid>
                    </Grid>
                    <Grid item>
                        <Grid container alignItems='center'>                            
                            <ClearRoundedIcon onClick={()=>props.setAlertOpen(false)} style={{color:'#989898',width:'22px',height:'22px'}}/>
                        </Grid>
                    </Grid>
                </Grid>   
            </Snackbar>
        </Grid>
    )
}

export default SnackbarDev