const handleCheckedBoxSeleceted = (selecetedDay: string, currentData: string[]) =>
{
    const isExisted = _.includes(currentData, selecetedDay)
        if (isExisted)
        {
            return currentData.filter(x=> x!== selecetedDay)
        }
        else
        {
            return [...currentData,selecetedDay]
        }
}

