const CustomStepper = (props: StepIconProps, label: string, index: number) =>
{
    const { active, completed, className } = props;

    return (
        <Grid container alignItems='center'>
            <Grid item style={{ marginRight: '8px' }}>
                <Grid container justifyContent='center' alignItems='center' style={{ width: '40px', height: '40px', borderRadius: 50, border: index < 2 ? '2px solid #5A91FF' : '2px solid #C4C4C4' }}>
                    <Grid container justifyContent='center' alignItems='center' style={{ width: '32px', height: '32px', borderRadius: 50, backgroundColor: index < 2 ? '#5A91FF' : '' }}>
                        <Typography style={{ fontFamily: 'Noto sans', color: index < 2 ? '#FFF' : '#C4C4C4', fontSize: '18px' }}>{`${ index + 1 }`}</Typography>
                    </Grid>
                </Grid>
            </Grid>
            <Grid item>
                <Typography style={{ fontFamily: 'Noto sans',  color: index < 2 ? '#5A91FF' :'#C4C4C4' , fontSize: '18px' }}>{label}</Typography>
            </Grid>
        </Grid>
    )
}
    
    

const Schedule = (props:Props) :React.ReactElement<Props>  =>  {

    const classes = useStyles()
    const history = useHistory()
    const theme = useTheme()
    const matchesMac = useMediaQuery(theme.breakpoints.down('xl'));
    const dispatch = useDispatch()
    const tags = useSelector((state: RootState) => state.audienceTag)
    const scheduleData = useSelector((state: RootState) => state.audienceForm.schedule)

    const audienceForm = useSelector((state: RootState) => state.audienceForm)
    const [tab, setTab] = useState(0)
    
    const [alertOpen, setAlertOpen] = React.useState<boolean>(false);

    const steps = [
        'Filter',
        'Schedule',
        'Comfirm',
        'Publish',
    ];

    const tabList = [
        'Once',
        'Mon-Fri',
        'Weekly',
        'Monthly',
        'Yearly'
    ]

    const bannerData = [
        { key: 'KFC', name: 'KFC', value: 'KFC' },
        { key: 'PHHK', name: 'Pizza Hut', value: 'PHHK' },
        { key: 'PHDHK', name: 'PHD', value: 'PHDHK' }
    ]

    useEffect(() =>
    {
        const tabIndex = _.findIndex(tabList, x => x === scheduleData.period) === -1 ? 0 : _.findIndex(tabList, x => x === scheduleData.period)
        setTab(tabIndex)
        
    },[scheduleData])

    return(
        <Grid container direction='column' alignItems='center' className={classes.main}>
            {/* title and breadcurumbs */}
            <Grid item container alignItems='center' className={classes.titleContainer} direction='column'>
                <Grid item container direction='column' >
                    <Grid item container>
                        <Breadcrumbs
                            separator={<NavigateNextIcon fontSize="small" />}
                            aria-label="breadcrumb"
                        >
                                <Link underline="hover" color="inherit" href="/">
                                <Typography className={classes.h2}>{'Home'}</Typography>
                            </Link>
                            <Link
                                underline="hover"
                            >
                                <Typography className={classes.h2} style={{color:'#5A91FF'}}>{'Create a new audience'}</Typography>
                            </Link>
                        </Breadcrumbs>
                    </Grid>
                    <Grid item container  alignItems='center' style={{marginTop:'10px'}} >
                        <Grid item lg={3} md={3}>
                            <Typography className={classes.h1}>{'Create a new audience'}</Typography>
                        </Grid>
                        <Grid item lg={9} md={9}>
                            <Grid container justifyContent='flex-end' alignItems='center' >
                                <AudienceBar disable={false}/>
                                <Tags disable={false}/>
                                <Grid item >
                                    <Button variant="contained" disableRipple style={{ border: '2px solid #5A91FF', backgroundColor: '#F1F4F6', color: '#5A91FF' }} className={classes.nextButton} onClick={() =>
                                    {
                                        history.push('/audience')
                                        dispatch(unmount_audience_details())
                                    }} >{'Cancel'}</Button>
                                </Grid>
                                <Grid item style={{marginRight:'22px',marginLeft:'22px'}}>
                                    <Button variant="contained"  disableRipple className={classes.nextButton} onClick={()=> history.push('/audience/new')} >{'Previous'}</Button>
                                </Grid>
                                <Grid item>
                                    <Button variant="contained" disableRipple className={classes.nextButton} onClick={() =>
                                    {
                                        if (audienceForm.name === '')
                                        {
                                            setAlertOpen(true)
                                        }
                                        else
                                        {
                                            history.push('/audience/new/confirm')
                                        }
                                    }}>{'Next'}</Button>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
            {/* title and breadcurumbs */}
            <Grid item container alignItems='center' className={classes.mainContainer} direction='column'>
                <Grid container justifyContent='center' style={{marginTop:'40px',marginBottom:'40px'}}>
                    <Grid item style={{width:matchesMac?'45%':'40%'}}>
                        <Stepper nonLinear activeStep={1}>
                            {steps.map((label, index) => (
                            <Step key={label} >
                                <StepLabel color="inherit" StepIconComponent={(element) => CustomStepper(element,label,index)} onClick={()=>console.log('hi')}/>
                            </Step>
                            ))}
                        </Stepper>
                    </Grid>
                </Grid>
                <Grid container alignItems='center'  direction='column'  style={{width:'100%'}}>
                    <Grid item container justifyContent='flex-start' style={{width:'47%'}} >
                        {
                            tabList.map((tabItem,index)=>{
                                return(
                                    <Grid item key={index} >
                                        <Button disableRipple className={classes.tab} style={tab === index ? { width: '160px', backgroundColor: '#FFF' } : {}} onClick={() =>
                                        {
                                            dispatch(handleAudienceFormForSchedule({ ...scheduleData, ...{ period: tabItem }}))
                                            setTab(index)
                                        }}>
                                            <Typography className={classes.tabFont} style={ tab === index ? {color:'#4A78DF',fontWeight:700}: {}} >{tabItem}</Typography>
                                        </Button>
                                    </Grid>
                                )
                            })
                        }
                       
                    </Grid>
                    <Grid item container justifyContent='center' style={{height:'320px',width:'47%',backgroundColor:'#FFF',borderRadius: '0px 10px 10px 10px'}}>
                        <ScheduleContent tab={tab}/>
                    </Grid>
                </Grid>
            </Grid>
            <Snackbar wording={'Audience name is required !'} alertOpen={alertOpen} setAlertOpen={setAlertOpen}/>
        </Grid>
    )
}

export default Schedule